import React from "react"
import PropTypes from "prop-types"
import 'aos/dist/aos.css';


class GiantHeader extends React.Component {



    componentDidMount() {
        const AOS = require('aos');
        this.aos = AOS
        this.aos.init()

    }

    componentDidUpdate() {
        this.aos.refresh()
    }

    render() {
        return (
            <h1 css={{
                fontFamily: "Open Sans",
                fontWeight: '700',
                fontSize: this.props.fontsize,
                textTransform: 'uppercase',
                WebkitTextStroke: this.props.stroke,
                textStroke: this.props.stroke,
                color: this.props.color,
                opacity: this.props.opacity,
                textShadow: '1px 3px 2px rgba(0,0,0,.75)'
            }}>{this.props.text}</h1>)
    }



}

GiantHeader.propTypes = {
    text: PropTypes.string

}

GiantHeader.defaultProps = {
    fontsize: '250px'
}

export default GiantHeader
