import React from "react"
import SEO from "../components/seo"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import IndexImage from "../images/index-image.jpg"
import RelaxImage from "../images/relax-image.jpg"
import { LOGO_COLOR_DARK } from "../components/siteconfig"
import GiantHeader from "../components/giantheader"
import 'aos/dist/aos.css';
import "./styles.css";

class IndexPage extends React.Component {
  constructor() {
    super()
    this.state = {
      isActive: "index"
    }
  }

  componentDidMount() {
    const AOS = require('aos');
    this.aos = AOS
    this.aos.init()
    document.querySelector('html').style.overflowY = "hidden";
  }
  componentDidUpdate() {
    this.aos.refresh()
  }
  render() {
    return (
      <div className="index-background" style={{
        display: `flex`,

        width: `100%`,
        height: `2000px`,
        overflow: `hidden`

      }}>
        <SEO title="Relax - 1240 Greene St" />
        <div css={{ width: '50%' }} >
          <Logo title="1240 Greene St" color={LOGO_COLOR_DARK} />

        </div>

        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <Nav isActive={this.state.isActive} />
          <div css={{
            position: 'fixed',
            bottom: '-117px',
            left: '-71%',
            transform: 'scale(.4)',
            '@media(min-width:700px)': {
              bottom: '-60px',
              transform: 'scale(1)',
              left: '5%',
              width: '120%'

            }
          }}>
            <div data-aos="fade-left" data-aos-delay="350" data-aos-duration="500" data-aos-easing="ease-in-sine">
              <GiantHeader text="modern style" color="#666666" opacity=".5" stroke="1px #000" fontsize="160px" />
            </div>
          </div>
        </div>


      </div >
    )
  }
}
